.btn-indigo {
  @apply px-6 py-3 rounded bg-irvan-blue text-white text-sm font-bold whitespace-no-wrap transition delay-75 duration-300 ease-in-out;

  &:hover, &:focus { @apply bg-orange-500 }
}
.btn-tosca {
  @apply px-6 py-3 rounded bg-tosca-800 text-white text-sm font-bold whitespace-no-wrap transition delay-75 duration-300 ease-in-out;

  &:hover, &:focus { @apply bg-tosca-900 }
}
.btn-green {
  @apply px-6 py-3 rounded bg-green-600 text-white text-sm font-bold whitespace-no-wrap transition delay-75 duration-300 ease-in-out;

  &:hover, &:focus { @apply  bg-green-800 }
}
.btn-red {
    @apply px-6 py-3 rounded bg-red-600 text-white text-sm font-bold whitespace-no-wrap transition delay-75 duration-300 ease-in-out;

    &:hover, &:focus { @apply  bg-red-800 }
}

.btn-disable{
    @apply px-6 py-3 rounded bg-gray-500 text-white text-sm font-bold whitespace-no-wrap;
}
.btn-spinner,
.btn-spinner:after {
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
}

.btn-spinner {
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: .2em solid white;
  border-right: .2em solid white;
  border-bottom: .2em solid white;
  border-left: .2em solid transparent;
  transform: translateZ(0);
  animation: spinning 1s infinite linear;
}

@keyframes spinning {
  0% { transform: rotate(0deg) }
  100% { transform: rotate(360deg) }
}
